import React, { useState, useEffect } from "react";
import { WebMidi } from "webmidi";
import "./App.css";

function App() {
  const [midiOutput, setMidiOutput] = useState(null);
  const [eqValues, setEqValues] = useState({
    tranche31: 64,
    master: 64, // Valeur par dfaut au centre (0-127)
  });

  useEffect(() => {
    WebMidi.enable((err) => {
      if (err) {
        console.error("WebMidi n'a pas pu tre activ.", err);
      } else {
        console.log("WebMidi activ!");
        const output = WebMidi.outputs[0];
        if (output) {
          setMidiOutput(output);
        } else {
          console.log("Aucune sortie MIDI disponible.");
        }
      }
    });
  }, []);

  // Fonction de conversion des valeurs
  const convertToEqValue = (value) => {
    return (value / 128) * 32 - 16; // Convertir 0-127 en -16  +16
  };

  const handleRangeChange = (e) => {
    // console.log(0xbd, e.target.id);
    const { name, value } = e.target;
    setEqValues((prevValues) => ({
      ...prevValues,
      [name]: value,
    }));
    if (midiOutput) {
      const channel = 14; // Canal MIDI 14
      if (parseInt(e.target.id) % 2 === 1) {
        console.log("impair");
        const controlChangeNumberL = parseInt(e.target.id);
        const controlChangeNumberR = parseInt(e.target.id * 2) + 1;
        console.log(controlChangeNumberL, controlChangeNumberR);
        midiOutput.channels[channel].sendControlChange(
          controlChangeNumberL,
          parseInt(value)
        );
        midiOutput.channels[channel].sendControlChange(
          controlChangeNumberR,
          parseInt(value)
        );
      } else {
        console.log("pair");
        const controlChangeNumberL = parseInt(e.target.id);
        const controlChangeNumberR = parseInt(e.target.id * 2);
        console.log(controlChangeNumberL, controlChangeNumberR);
        midiOutput.channels[channel].sendControlChange(
          controlChangeNumberL,
          parseInt(value)
        );
        midiOutput.channels[channel].sendControlChange(
          controlChangeNumberR,
          parseInt(value)
        );
      }
    }
  };

  return (
    <div className="App">
      <h1>Contrleur MIDI pour Ultracurve Pro DSP8024</h1>
      <div className="slider-container">
        <div className="slider">
          <span className="value">
            {convertToEqValue(eqValues.tranche31).toFixed(1)} dB
          </span>
          <input
            type="range"
            name="tranche31"
            className="form-range"
            min="0"
            max="128"
            step="2"
            id="31"
            value={eqValues.tranche31}
            onChange={handleRangeChange}
            orient="vertical"
          />
          <label htmlFor="31" className="form-label">
            20KHz
          </label>
        </div>
        <div className="slider">
          <span className="value">
            {convertToEqValue(eqValues.master).toFixed(1)} dB
          </span>
          <input
            type="range"
            name="master"
            className="form-range"
            min="0"
            max="128"
            step="2"
            id="32"
            value={eqValues.master}
            onChange={handleRangeChange}
            orient="vertical"
          />
          <label htmlFor="32" className="form-label">
            MASTER
          </label>
        </div>
      </div>
    </div>
  );
}

export default App;
